@font-face {
	font-family: "opendyslexic";
	src: url("/fonts/opendyslexic/OpenDyslexic-Regular.otf");
	font-style: normal;
	font-weight: normal;
  }

  @font-face {
	font-family: "opendyslexic";
	src: url("/fonts/opendyslexic/OpenDyslexic-Italic.otf");
	font-style: italic;
	font-weight: normal;
  }

  @font-face {
	font-family: "opendyslexic";
	src: url("/fonts/opendyslexic/OpenDyslexic-Bold.otf");
	font-weight: bold;
	font-style: normal;
  }

  @font-face {
	font-family: "opendyslexic";
	src: url("/fonts/opendyslexic/OpenDyslexic-BoldItalic.otf");
	font-weight: bold;
	font-style: italic;
  }

  @font-face {
	font-family: "opendyslexicmono";
	src: url("/fonts/opendyslexic/OpenDyslexicMono-Regular.otf");
	font-weight: normal;
	font-style: normal;
  }

body {
	margin: 0;
	padding: 0;
	font-family: opendyslexic;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: opendyslexicmono;
}

.body {
	height: 100%;
	width: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: center;
	        justify-content: center;
	color: #2a2e45;
}
a {
	color: #d7dcff;
	text-decoration: none;
}

a:hover {
	color: #b9a4f6;
}

p {
	margin-top: 10px;
	margin-bottom: 10px;
	margin-left: 5px;
	margin-right: 5px;
}

.headline {
	font-size: 45px;
	margin-top: 15%;
	margin-bottom: 20px;
}

.content {
	max-width: 800px;
	-webkit-justify-content: center;
	        justify-content: center;
}

.center {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
}

.box {
	color: white;
	background-color: #2a2e45;
	padding: 10px;
	border-radius: 5px;
	border-color: #2a2e45;
}

.spin{
    display:inline-block;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
}

.spin:hover{
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg);
}

.rotate-cw-180 {
    display:inline-block;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
}

.rotate-cw-180:hover{
    -webkit-transform: rotateZ(-180deg);
            transform: rotateZ(-180deg);
}

