.body {
	height: 100%;
	width: 100%;
	display: flex;
	flex-direction: row;
	justify-content: center;
	color: #2a2e45;
}
a {
	color: #d7dcff;
	text-decoration: none;
}

a:hover {
	color: #b9a4f6;
}

p {
	margin-top: 10px;
	margin-bottom: 10px;
	margin-left: 5px;
	margin-right: 5px;
}

.headline {
	font-size: 45px;
	margin-top: 15%;
	margin-bottom: 20px;
}

.content {
	max-width: 800px;
	justify-content: center;
}

.center {
	display: flex;
	justify-content: center;
}

.box {
	color: white;
	background-color: #2a2e45;
	padding: 10px;
	border-radius: 5px;
	border-color: #2a2e45;
}

.spin{
    display:inline-block;
    transition: transform 1s;
}

.spin:hover{
    transform: rotateY(-180deg);
}

.rotate-cw-180 {
    display:inline-block;
    transition: transform 1s;
}

.rotate-cw-180:hover{
    transform: rotateZ(-180deg);
}
